.info_for_product {
  width: 240px;
  margin-right: 20px;
}
.info_for_product__timer {
  margin-bottom: 10px;
}
.info_for_product_title {
  font-size: 36px;
  line-height: 110%;
}
.info_for_product_subtitle {
  font-size: 22px;
  margin-top: 20px;
  margin-bottom: 30px;
  line-height: 130%;
}
.info_for_product_btn {
  cursor: pointer;
  transition: all 0.2s;
  font-size: 16px;
  line-height: 21px;
  width: 240px;
  padding: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border: 1px solid #a46c46;
  background: transparent;
  border-radius: 8px;
  transition: all 0.2s;
}
.info_for_product_btn.mob-btn {
  display: none;
}
.info_for_product_btn:hover {
  color: #fff;
  background: #a46c46;
}
@media (max-width: 1024px) {
  .info_for_product {
    width: 100%;
    margin: 0 0 24px 0;
    text-align: center;
  }
  .info_for_product__timer {
    margin-bottom: 8px;
    font-size: 28px;
  }
  .info_for_product__timer-subtitle {
    font-size: 14px;
  }
  .info_for_product_title {
    font-size: 24px;
  }
  .info_for_product_subtitle {
    font-size: 14px;
    margin: 8px 0 0;
  }
  .info_for_product .info_for_product_btn {
    display: none;
  }
  .info_for_product_btn {
    padding: 12px 45px;
  }
  .info_for_product_btn.mob-btn {
    display: block;
    max-width: 349px;
    width: 100%;
    margin-top: 24px;
  }
}
@media (max-width: 600px) {
  .info_for_product_btn {
    font-size: 12px;
  }
}

.slider__block {
  width: calc(100% - 260px);
}
.slider__wrapper {
  display: flex;
}
.slider__wrapper .swiper {
  margin-left: 0;
}
@media (max-width: 1440px) {
  .slider__wrapper .blog_item {
    width: 328px;
  }
}
@media (max-width: 1024px) {
  .slider__wrapper {
    flex-wrap: wrap;
    justify-content: center;
  }
  .slider__wrapper .catalog-swiper .cart_item {
    width: 176px;
  }
}
@media (max-width: 600px) {
  .slider__wrapper .catalog-swiper .cart_item {
    width: 168px;
  }
}
@media (max-width: 400px) {
  .slider__wrapper .blog_item {
    width: 100%;
  }
}

.catalog_slider {
  padding-bottom: 120px;
}
@media (max-width: 1024px) {
  .catalog_slider {
    padding-bottom: 80px;
  }
}
@media (max-width: 720px) {
  .catalog_slider {
    padding-bottom: 48px;
  }
}

.catalog-swiper .cart_item {
  width: 312px;
}
.catalog-swiper .swiper-button-next,
.catalog-swiper .swiper-button-prev {
  background: #ffffff;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  transition: all 0.3s;
}
.catalog-swiper .swiper-button-next::after,
.catalog-swiper .swiper-button-prev::after {
  color: #453f3a;
  font-size: 25px;
}
.catalog-swiper .swiper-button-next:hover,
.catalog-swiper .swiper-button-prev:hover {
  transform: scale(1.1);
  background: #453f3a;
}
.catalog-swiper .swiper-button-next:hover::after,
.catalog-swiper .swiper-button-prev:hover::after {
  color: #ffffff;
}