.nav-sidebar {
  max-width: 240px;
}
.nav-sidebar__items li {
  border-bottom: 1px solid #ebeae8;
  transition: all 0.3s;
}
.nav-sidebar__items li a {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 8px 12px;
}
.nav-sidebar__items li a span {
  line-height: 130%;
}
.nav-sidebar__items li a img {
  min-width: 16px;
  width: 16px;
  height: 16px;
  margin-left: 15px;
}
.nav-sidebar__items li a:hover {
  color: #a46c46;
  transform: scale(1.1);
}