.terms-and-policies {
  display: flex;
  padding: 40px 0 104px;
}
.terms-and-policies__sidebar {
  position: sticky;
  top: 10%;
  min-width: 240px;
  height: 100%;
}
.terms-and-policies__content {
  margin-left: 25px;
}
.terms-and-policies__title {
  margin: 0;
}
.terms-and-policies__items {
  max-width: 900px;
  width: 100%;
  margin-top: 24px;
}
.terms-and-policies__block-item {
  color: #453f3a;
  font-size: 16px;
  line-height: 130%;
}
.terms-and-policies__block-item + .terms-and-policies__block-item {
  margin-top: 45px;
}
.terms-and-policies__block-item-title.--top-indent {
  margin-top: 15px;
}
.terms-and-policies__block-item-subtitle {
  margin: 15px 0;
}
.terms-and-policies__block-item-text {
  margin-top: 15px;
  color: #453f3a;
  font-weight: 400;
}
.terms-and-policies__block-item-text p + p,
.terms-and-policies__block-item-text li + li {
  margin-top: 15px;
}
@media (max-width: 1024px) {
  .terms-and-policies {
    padding: 40px 0 80px;
  }
}
@media (max-width: 828px) {
  .terms-and-policies {
    padding: 18px 0 80px;
  }
}
@media (max-width: 720px) {
  .terms-and-policies {
    padding: 18px 0 48px;
  }
  .terms-and-policies__sidebar {
    display: none;
  }
  .terms-and-policies__content {
    margin-left: 0;
  }
  .terms-and-policies__items {
    max-width: none;
    margin-top: 16px;
  }
}
@media (max-width: 500px) {
  .terms-and-policies__block-item {
    font-size: 12px;
  }
  .terms-and-policies__block-item + .terms-and-policies__block-item {
    margin-top: 35px;
  }
  .terms-and-policies__block-item-title.--top-indent {
    margin-top: 10px;
  }
  .terms-and-policies__block-item-subtitle {
    margin: 10px 0;
  }
  .terms-and-policies__block-item-text {
    margin-top: 10px;
  }
  .terms-and-policies__block-item-text p + p,
  .terms-and-policies__block-item-text li + li {
    margin-top: 10px;
  }
}